<template>

<b-container>
       <b-row>
            <b-col class="text-center">
                <h5>AIIAS People Page</h5>
            </b-col>
        </b-row>
        <b-row>
            <b-col class="text-center">
                <b-form-group
                    label="Filter"
                    label-cols-sm="1"
                    label-align-sm="left"
                    label-size="sm"
                    label-for="filterInput"
                >
                    <b-input-group size="sm">
                        <b-form-input
                            v-model="filter"
                            type="search"
                            id="filterInput"
                            placeholder="Type to Search"
                        ></b-form-input>
                
                        <b-input-group-append>
                            <b-button :disabled="!filter" @click="filter = ''">Clear</b-button>
                        </b-input-group-append>
                    </b-input-group>
                </b-form-group>
            </b-col>
        </b-row>
        <b-row>
            <b-col>
                <!-- pagination component  -->
                <b-pagination
                    v-model="currentPage"
                    :total-rows="rows"
                    :per-page="perPage"
                    aria-controls="my-table-meetings"
                    align="left"
                    label="Sort"
                >
                </b-pagination>
            </b-col>
            <b-col class="text-left"> 
                <p>Current Page: {{ currentPage }}</p>
            </b-col>
            <b-col> 
                <div class="text-right">
                    <!-- Pass modal that will be visualized and pass the first component to be focused -->
                    <b-button variant="success"  size="sm" @click="setPeopleEdit('0')" v-b-modal.modalEditPeoples>
                        Add Person
                    </b-button>
                </div>
            </b-col>
        
        </b-row>
        <b-row>
            <b-col>
                <!-- main table -->
                <b-table
                    class="text-left"
                    id="my-table-people"
                    :items="peoples"
                    :fields="fields"
                    :per-page="perPage"
                    :current-page="currentPage"
                    :sort-by.sync="sortBy"
                    :sort-desc.sync="sortDesc"
                    :filter="filter"
                    :filterIncludedFields="filterOn"
                    @filtered="onFiltered"
                    sort-icon-left
                    responsive="sm"
                    small
                >
                    <!-- Buttons -->
                    <template v-slot:cell(actions)="peoples">
                        <b-button class="mr-2" variant="primary" size="sm" @click="setPeopleEdit(peoples.item.id)"  v-b-modal.modalEditPeoples>
                            Edit
                        </b-button>
                        <b-button class="mr-2" variant="danger" size="sm" v-on:click="deleteData(peoples.item.id, peoples.item.last_name)">
                            Delete
                        </b-button>
                    </template>
                </b-table>                 
            </b-col>
        </b-row>
        <b-row>
            <b-col class="modal-footer">
                
            </b-col>
        </b-row>
    
        <ModalEditPeople :people="people" :countries="countries" />
    </b-container>
</template>


<script>
    import ModalEditPeople from '../components/ModalEditPeople.vue'

    export default {
        name: 'AIIASPeoples',
        components: {
            ModalEditPeople
        },
        created() {
            this.fetchPeoples()
            this.fetchCountries()
        },
        data() {
            return {
                selected: null,
                countries: [],
                filter: null,
                filterOn: [],    
                sortBy: 'Name',
                sortDesc: false,
                fields: [
                    {
                        key: 'last_name', 
                        label: 'Name, First Name',
                        sortable: true,
                        sortDirection: 'desc'
                    },
                    {
                        key: 'email', 
                        label: 'Email',
                        sortable: true
                    },
                    {
                        key: 'actions', 
                        label: 'Actions'
                    }
                ],
                peoples: [],
                

                people: {
                    date_updated: '',
                    last_name: '',
                    first_name: '',
                    middle_name: '',
                    email: '',
                    address: '',
                    country_id: ''
                },
                
                modal: null,
                
                perPage: 10,
                currentPage: 1
            }
        },
        created() {
            this.fetchPeoples()
            this.fetchCountries()
        },
        computed: {
            rows() {
                return this.peoples.length
            },
            nameState() { 
                return this.people.last_name.length > 2 ? true : false
            }
        },
        methods: {
            setPeopleEdit(people_id) {
                this.people.id = people_id
                this.people.last_name = ''
                this.people.first_name = ''
                this.people.middle_name = ''
                this.people.email = ''
                this.people.address = ''
                this.people.country_id = ''
            },
            onFiltered(filteredItems) {
                // Trigger pagination to update the number of buttons/pages due to filtering
                this.totalRows = filteredItems.length
                this.currentPage = 1
            },
            //this fetch is necessary to repeat for every table in each file.
            fetchPeoples() {

                db.collection('people').orderBy('last_name', 'desc').onSnapshot((querySnapshot) => {
                    let allPeoples=[]
                
                    querySnapshot.forEach(doc => {
                        const dbPeoples = {
                            //use de show beacause de edit form, visualization matters
                            id: doc.id,
                            last_name: doc.data().last_name + ', ' + doc.data().first_name,
                            //first_name: doc.data().first_name,
                            //middle_name: doc.data().middle_name,
                            email: doc.data().email
                            //address: doc.data().address,
                            //country_id: doc.data().country_id 
                        }
                        allPeoples.push(dbPeoples)
                    })

                    this.peoples=allPeoples
                })    
                
            },
            //this method is used to get values from countries and added to the select
            fetchCountries() {
                db.collection('country').orderBy('name', 'desc').onSnapshot((querySnapshot) => {
                    let allCountries=[]

                    allCountries.push({ value: '', text: '<Please select the country>' })

                    querySnapshot.forEach(doc => {
                        const dbCountry = {
                            value: doc.id,
                            text: doc.data().name
                        }
                        allCountries.push(dbCountry)
                    })
                    
                    
                    this.countries=allCountries
                })
            },
            deleteData(id, name) {
                this.$bvModal.msgBoxConfirm('Are you sure want to delete: ' + name + ' ?')
                .then(value => {
                    if (value) {
                    db.collection('people')
                        .doc(id)
                        .delete()
                        .then(function() {
                            console.log("Document deleted with ID - deleteData(): ", id)
                        })
                        .catch(function(error) {
                            console.error("Error deleting document - deleteData(): ", error)
                        })
                

                    this.$bvModal.msgBoxOk('Document was deleted successfully', {
                        title: 'Confirmation',
                        size: 'sm',
                        buttonSize: 'sm',
                        okVariant: 'success',
                        headerClass: 'p-2 border-bottom-0',
                        footerClass: 'p-2 border-top-0',
                        centered: true
                        })
                        .then(value => {
                            this.boxTwo = value
                        })
                        .catch(err => {
                            // An error occurred
                        })
                    }
                })
                .catch(err => {
                    console.error("Error deleting document - deleteData(): ", err)
                })

            }

        }
          
    }
</script>